.container{
    width: 270px;
    box-shadow: 0px 4px 30px rgba(131, 158, 252, 0.3);
    border-radius: 5px;
    position: relative;
    cursor: pointer !important;
    background-color: white;
    margin-top: 30px;

    transition: all .2s linear;
}

.container:hover {
    transform: translateY(-3px);
}

.institute{
    position: absolute;
    right: 5px;
    top: 5px;
    border-radius: 50%;
    transition: all .2s linear;
}

.institute:hover {
    background: #F2C94C;
}

.imgDiv{
    height: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.img{
    height: 186px;
}

.titleDiv{
    background: var(--main-btn-blue);
    padding: 14px 0;
    border-radius: 0 0 5px 5px;
    transition: all .2s linear;
}

.titleDiv span {
    font-size: 18px;
    font-family: mRegular;
    display: flex;
    justify-content: center;
}

.active {
    background: var(--main-btn-orange);

    transition: all .2s linear;
}

.modalEnter{
    opacity: 0;
}

.modalEnterActive{
    opacity: 1;
    transition: opacity 1000ms;
}
